html {
  height: 100%;
  box-sizing: border-box;
}

body {
  background-color: #ececec03;
  position: relative;
  padding-bottom: 6rem;
  min-height: 100%;
}

.codePreview {
  min-height: 100%;
  margin: 0 !important;
  padding: 0 !important;
  overflow: unset !important;
  min-width: max-content;
}

.lineNumbers {
  margin-right: 10px;
  padding-right: 5px !important;
  border-right: 1px dashed;
  height: 100%;
  min-width: 24px;
}

.grid-container {
  padding-top: 48px;
  height: 100%;
}

.grid-column {
  height: 100%;
  overflow-y: auto;
  padding-left: 10px;
  padding-top: 10px;
  border-top: 1px solid;
}

.modal-content h4 {
  padding-bottom: 10px;
  margin-bottom: 0;
  position: fixed;
}

[type="radio"]:checked + span:after, [type="radio"].with-gap:checked + span:after {
  background-color: #000000;
}

[type="radio"]:checked + span:after, [type="radio"].with-gap:checked + span:before, [type="radio"].with-gap:checked + span:after {
  border: 2px solid #000000;
}

.modal {
  width: 35%;
}

.modal.preview {
  width: 75%;
  max-height: 90%;
  height: 90%;
}

.mn-footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
}

.mn-logo {
  margin-top: 40px;
}

.mn-footer-logos {
  float: right;
  margin-left: 10px;
}

.mn-feature-selection:hover {
  background-color: black !important;
}

.mn-feature-selection:hover > div.card-content > span.card-title {
  color: white !important;
}

.mn-container {
  margin-top: 5rem;
}

.mn-input:focus {
  border-bottom: 1px solid #000000 !important;
  box-shadow: 0 1px 0 0 #000000 !important;
}

.progress {
  background-color: #dfdfdf;
}

.progress .indeterminate {
  background-color: #040404bd;
}

.spinner-blue, .spinner-blue-only {
  border-color: #000000;
}
